import React, {useState} from 'react';
import {Tooltip} from 'reactstrap';
import {useSpring, animated} from 'react-spring';

//QR Code Animation vars
const calc = (x, y) => [-(y - window.innerHeight / 2) / 20, (x - window.innerWidth / 2) / 20, 1.1]
const trans = (x, y, s) => `perspective(1200px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`


const Hero = () =>{
  const [tooltipOpen, setTooltipOpen] = useState(false);
  const toggle = () => setTooltipOpen(!tooltipOpen);

  //State for QR code animation
  const [props, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 40 } }))


    return(
      <div className="HeroMain">
        <div className="container-fluid">
          <div className="row p-4">
            <div className="col-sm-12 col-md-8 col-lg-8">
              <div id="spacer20"></div>
              <br/>
              <br/>
              <h1 className="display-1 text-primary" style={{mixBlendMode: "difference"}}>Welcome to FaceIT</h1>
              <h4>Your one stop solution to local data-privacy</h4>
              <br/>
              <div className="container-fluid">
                <div className="output" id="output">
                  <h1 className="cursor"> </h1>
                  <p></p>
                </div>
              </div>

            </div>
            
            <div className="col-sm-12 col-md-4 col-lg-4">
              <div className="text-center container p-3">
                <div id="spacer10"></div>
                <animated.div
                  class="qr"
                  onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
                  onMouseLeave={() => set({ xys: [0, 0, 1] })}
                  style={{ transform: props.xys.interpolate(trans) }}
                  id="QRTooltip"
                />
                <Tooltip placement="bottom" isOpen={tooltipOpen} target="QRTooltip" toggle={toggle}>
                    Got stuck? Watch our comprehensive setup video!
                </Tooltip>
              </div>
            </div>
          </div>
        </div>      
      </div>
    );
}




export default Hero;