import Home from "views/Home.js";
import Login from "components/Login/Login.js";
import Register from "components/Register/Register.js";
import Google from "components/Google/Google.js";
import Facebook from "components/Facebook/Facebook.js";
import ForgotPassword from "components/ForgotPassword/ForgotPassword.js";
import NotFoundPage from "components/ErrorPages/NotFoundPage.js";
import Activate from "components/Activate/Activate";
import ForgotPasswordConfirm from "components/ForgotPasswordConfirm/ForgotPasswordConfirm.js";

var CommonRoutes = [
  {
    path: "/home",
    name: "Home",
    icon: "tim-icons icon-chart-pie-36",
    component: Home,
    layout: "/common"
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    layout: "/common"
  },
  {
    path: "/register",
    name: "Register",
    component: Register,
    layout: "/common"
  },
  {
    path: "/google",
    name: "Google",
    component: Google,
    layout: "/common"
  },
  {
    path: "/facebook",
    name: "Facebook",
    component: Facebook,
    layout: "/common"
  },
  {
    path: "/activate/:uid/:token",
    name: "Activate",
    component: Activate,
    layout: "/common"
  },
  {
    path: "/forgot_password",
    name: "ForgotPassword",
    component: ForgotPassword,
    layout: "/common"
  },
  {
    path: "/password/reset/confirm",
    name: "ForgotPasswordConfirm",
    component: ForgotPasswordConfirm,
    layout: "/common"
  },
  {
    path: "/404",
    name: "PageNotFound",
    component: NotFoundPage,
    layout: "/common"
  },
];
export default CommonRoutes;
