import React, {useRef, useEffect} from "react";
import { Route, Switch, Redirect } from "react-router-dom";
// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";

// core components
// import CommonNavbar from "components/Navbars/CommonNavbar.js";
import CommonNav from "components/Navbars/CommonNav.js";
import Footer from "components/Footer/Footer.js";
// import CommonSidebar from "components/Sidebar/CommonSidebar.js";
import CommonRoutes from "CommonRoutes.js";
//import logo from "assets/img/logo.svg";

import { connect } from 'react-redux';
import { checkAuthenticated, load_user } from '../../actions/auth';




//Layout = ({ checkAuthenticated, load_user, children })

const Admin = ({ checkAuthenticated, load_user }) => {
  useEffect(() => {
      checkAuthenticated();
      load_user();
  }, []);
  const mainPanel = useRef(0);
  let ps;
  useEffect(() =>{
    if (navigator.platform.indexOf("Win") > -1) {
      document.documentElement.className += " perfect-scrollbar-on";
      document.documentElement.classList.remove("perfect-scrollbar-off");
      ps = new PerfectScrollbar(document.getElementById('mainPanel'), { suppressScrollX: true });
      let tables = document.querySelectorAll(".table-responsive");
      for (let i = 0; i < tables.length; i++) {
        ps = new PerfectScrollbar(tables[i]);
      }
    }
  }, []);

  useEffect(()=>{
    if (navigator.platform.indexOf("Win") > -1) {
      ps.destroy();
      document.documentElement.className += " perfect-scrollbar-off";
      document.documentElement.classList.remove("perfect-scrollbar-on");
    }
  },[])

  // useEffect((e)=>{
  //   if (e.hist.action === "PUSH") {
  //     if (navigator.platform.indexOf("Win") > -1) {
  //       let tables = document.querySelectorAll(".table-responsive");
  //       for (let i = 0; i < tables.length; i++) {
  //         ps = new PerfectScrollbar(tables[i]);
  //       }
  //     }
  //     document.documentElement.scrollTop = 0;
  //     document.scrollingElement.scrollTop = 0;
  //     this.refs.mainPanel.scrollTop = 0;
  //   }
  // }, [])

  const getCommonRoutes = (CommonRoutes) => {
    return CommonRoutes.map((prop, key) => {
      if (prop.layout === "/common") {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  const getBrandText = (path) => {
    for (let i = 0; i < CommonRoutes.length; i++) {
      if (
        this.props.location.pathname.indexOf(
          CommonRoutes[i].layout + CommonRoutes[i].path
        ) !== -1
      ) {
        return CommonRoutes[i].name;
      }
    }
    return "Brand";
  };

  // const handleBgClick = () => {
  //   this.setState({ backgroundColor: 'primary' });
  // };
  
  return(
    <>
      <div className="wrapper">
        {/* <CommonSidebar
          {...this.props}
          CommonRoutes={CommonRoutes}
          bgColor={this.state.backgroundColor}
          logo={{
            outterLink: "https://www.github.com/tinshade/",
            text: "FaceIT",
            imgSrc: logo
          }}
          toggleSidebar={this.toggleSidebar}
        /> */}
        <div
          className="main-panel"
          ref={mainPanel}
          id="mainPanel"
          data="primary"
        >
          <CommonNav/>
          <Switch>
            {getCommonRoutes(CommonRoutes)}
            <Redirect from="*" to="/common/404"/>
          </Switch>
            <Footer fluid />
        </div>
      </div>
    </>
  );
}


export default connect(null, { checkAuthenticated, load_user })(Admin);


