import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';

import { connect } from 'react-redux';
import {Link, Redirect} from 'react-router-dom';
import {HashLink} from 'react-router-hash-link';
const CommonNav = ({logout, isAuthenticated, props}) => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  return (
    <div>
      {isAuthenticated ? <Redirect to="/admin/"/>: ''}
      <Navbar color="transparent" expand="md">
        <Link className="nav-brand" to="/"></Link>
        <NavbarToggler style={{color: "white", borderRadius:"100%"}} onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav pills className="mr-auto" navbar>
            <NavItem>
              <Link className="nav-link" to="/common/home">Home</Link>
            </NavItem>

            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                About
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                  <HashLink className="nav-link" to="/common/home#what">What</HashLink>
                </DropdownItem>
                <DropdownItem>
                  <HashLink className="nav-link" to="/common/home#why">Why</HashLink>
                </DropdownItem>
                <DropdownItem>
                  <HashLink className="nav-link" to="/common/home#who">Who</HashLink>
                </DropdownItem>
                {/* <DropdownItem divider /> */}
                
              </DropdownMenu>
            </UncontrolledDropdown>

            <NavItem>
              <HashLink className="nav-link" to="/common/home#features">Features</HashLink>
            </NavItem>

            {/* <NavItem>
              <HashLink className="nav-link" to="/common/home#pricing">Pricing</HashLink>
            </NavItem> */}

            <NavItem>
              <HashLink className="nav-link" to="/common/home#downloads">Downloads</HashLink>
            </NavItem>

            <NavItem>
              <HashLink className="nav-link" to="/common/home#support">Support</HashLink>
            </NavItem>
          </Nav>
          <Nav pills>
            <NavItem>
                <Link className='nav-link active' to="/common/login/">Login</Link>
            </NavItem>
            <span style={{width: "10px"}}/>
            <NavItem>
                <Link className="nav-link" to="/common/register/" style={{border:"1px solid #e14eca"}}>Register</Link>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, null)(CommonNav);