import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password } from '../../actions/auth';
const ForgotPassword = ({ reset_password }) => {

  const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        email: ''
    });

    const { email } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        reset_password(email);
        setRequestSent(true);
    };

    if (requestSent) {
        return <Redirect to='/' />
    }

  return (
    <div className="content">
      <div className="ForgotMain">
        <div className="container-fluid row">
          <div className="col-sm-12 col-lg-7 col-md-7">
            <div id='spacer20'></div><div id='spacer10'></div>
            <div className="container p-4 col-sm-12 col-md-10 col-lg-10">
              <br/>
              <br/>
              <h1 className="display-1 text-white">Forgot Password?</h1>
              <div className="col-sm-12 pt-2 col-md-10 col-lg-10">
                <h4>Worry not!
                  <br/>
                  <h6>We've got you covered!</h6>
                </h4>
              </div>
            </div>
          </div>
          <div className="col-sm-12 col-lg-5 col-md-5">
            <div id='spacer20'></div>
            <div className="container-fluid">
              <div className="card p-4 col-sm-12 col-md-10 col-lg-10 bg-white">
                <div className="p-2">
                  <h4 className="display-4" style={{color: "#e14eac"}}>Password Reset</h4>
                  <p className="text-muted">Enter your email to get a password reset link.</p>
                </div>
                <div className="card-body pt-4">
                  <form id="FPForm" onSubmit={e => onSubmit(e)}>
                    <div className="form-group">
                      <label for="email_id">Email Address</label>
                      <input 
                        className="form-control" 
                        placeholder="Your registerd email id" 
                        type="email" 
                        id="email_id"
                        name='email'
                        value={email}
                        style={{color: "#333"}}
                        required
                        onChange={e => onChange(e)}
                      />
                    </div>
                    <br/>
                    <br/>
                    <div className="contianer text-center">
                      <button className="btn btn-primary" type='submit' id="log_btn">Reset</button>
                    </div>
                  </form>
                </div>
                <hr/>
                <div className="card-footer pt-2">
                  <small style={{color:"#333"}}>Remember it now? <Link to="/common/login/" title="Log back in to FaceIT!">Sign in!</Link></small>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  ); 
}

export default connect(null, { reset_password })(ForgotPassword);
