import React, {useState, useEffect} from "react";
import { Line, Bar } from "react-chartjs-2";
import axios from 'axios';
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Col,
  Row,
} from "reactstrap";

import { connect } from 'react-redux';
import {
  chartExample1,
  chartExample3,
  chartExample4
} from "variables/charts.js";

const Dashboard = ({state}) =>{

  let s = {};
  if(state){
    s = state;
  }
  const id = s.id;
  console.log(id)
  

  const months = [
    "JAN",
    "FEB",
    "MAR",
    "APR",
    "MAY",
    "JUN",
    "JUL",
    "AUG",
    "SEP",
    "OCT",
    "NOV",
    "DEC"
  ]

  let ts = Date.now();
  let date_ob = new Date(ts);
  let month = date_ob.getMonth()+1;

  // !FOR ACTIVITY CHART START
  const [actsChartData, setActsChartData] = useState({})
  const actsChart = () =>{
    setActsChartData({
      labels:months,
      datasets:[
        {
          label: "Times Used",
          fill: true,
          backgroundColor: "rgba(29,140,248,0.2)",
          borderColor: "#1f8ef1",
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          pointBackgroundColor: "#1f8ef1",
          pointBorderColor: "rgba(255,255,255,0)",
          pointHoverBackgroundColor: "#1f8ef1",
          pointBorderWidth: 20,
          pointHoverRadius: 4,
          pointHoverBorderWidth: 15,
          pointRadius: 4,
          data: [100, 70, 90, 70, 85, 60, 75, 60, 90, 80, 110, 100]
        }
      ]
    })
  }
  const getActs = async(month) =>{
    try{
      const config={
        headers: {
          'Content-Type':'application/json',
          'Authorization':String(1)+'&'+month,
        }
      }
      const res = await axios.get('http://fazeit.herokuapp.com/activity/', config)
      if(res.data.length>0){
        return res.data[0]['times_used']
      }else{
        return 0;
      }
      
    }catch(err){
      console.log(err)
    }
  }
  const updateActsList = async() =>{
    let labels = [];
    let data = [];
    for(let i=0; i<=11; i++){
      if(i>12){
        const response = await getActs(months[i-12]);
        months[i-12] = response.length
        labels.push(months[i-12])
        data.push(response)
      }else{
        const response = await getActs(months[i]); 
        labels.push(months[i])
        data.push(response)
      }
    }
    setActsChartData({
      labels:labels,
      datasets:[
        {
          label: "Times Used",
          fill: true,
          backgroundColor: "rgba(29,140,248,0.2)",
          borderColor: "#1f8ef1",
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          pointBackgroundColor: "#1f8ef1",
          pointBorderColor: "rgba(255,255,255,0)",
          pointHoverBackgroundColor: "#1f8ef1",
          pointBorderWidth: 20,
          pointHoverRadius: 4,
          pointHoverBorderWidth: 15,
          pointRadius: 4,
          data:data
        }
      ]
    })
  };

// !FOR ACTIVITY CHART END


  // !FOR FILES CHART START
  const [filesChartData, setFilesChartData] = useState({})
  const filesChart = () =>{
    setFilesChartData({
      labels:['JAN', 'FEB', 'MAR', 'APR', 'MAY'],
      datasets:[
        {
          label: "Files Stored",
          fill: true,
          backgroundColor: 'rgba(72,72,176,0.1)',
          hoverBackgroundColor: 'rgba(72,72,176,0.1)',
          borderColor: "#d048b6",
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          data: [90, 27, 60, 12, 80]
        }
      ]
    })
  }
  const getFiles = async(month) =>{
    try{
      const config={
        headers: {
          'Content-Type':'application/json',
          'Authorization':String(1)+'&'+month,
        }
      }
      const res = await axios.get('http://fazeit.herokuapp.com/filez/', config)
      if(res.data.length<1){
        return 0
      }else if(res.data.length>0){
        return res.data.length
      }
    }catch(err){
      console.log(err)
    }
  }
  const updateFilesList = async() =>{
    let labels = [];
    let data = [];
    for(let i=month-1; i<month+5; i++){
      if(i>12){
        const response = await getFiles(months[i-12]);
        months[i-12] = response.length
        labels.push(months[i-12])
        data.push(response)
      }else{
        const response = await getFiles(months[i]); 
        labels.push(months[i])
        data.push(response)
      }
    }
    setFilesChartData({
      labels:labels,
      datasets:[
        {
          label: "Files Stored",
          fill: true,
          backgroundColor: 'rgba(72,72,176,0.1)',
          hoverBackgroundColor: 'rgba(72,72,176,0.1)',
          borderColor: "#d048b6",
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          data:data
        }
      ]
    })
  };

// !FOR FILES CHART END






  // !FOR THREAT CHART START
  const [threatChartData, setThreatChartData] = useState({})

  const threatChart = () =>{
    setThreatChartData({
      labels:['JAN', 'FEB', 'MAR', 'APR', 'MAY'],
      datasets:[
        {
          label: "Peekers spotted",
          fill: true,
          backgroundColor: 'rgba(23,232,10,0)',
          borderColor: "#00d6b4",
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          pointBackgroundColor: "#00d6b4",
          pointBorderColor: "rgba(255,255,255,0)",
          pointHoverBackgroundColor: "#00d6b4",
          pointBorderWidth: 20,
          pointHoverRadius: 4,
          pointHoverBorderWidth: 15,
          pointRadius: 4,
          data: [90, 27, 60, 12, 80]
        }
      ]
    })
  }
  const getThreats = async(month) =>{
    try{
      const config={
        headers: {
          'Content-Type':'application/json',
          'Authorization':String(1)+'&'+month,
        }
      }
      const res = await axios.get('http://fazeit.herokuapp.com/threats/', config)
      if(res.data.length<1){
        return 0
      }else if(res.data.length>0){
        return res.data.length
      }
    }catch(err){
      console.log(err)
    }
  }
  const updateThreatList = async() =>{
    let labels = [];
    let data = [];
    for(let i=month-1; i<month+5; i++){
      if(i>12){
        const response = await getThreats(months[i-12]);
        months[i-12] = response.length
        labels.push(months[i-12])
        data.push(response)
      }else{
        const response = await getThreats(months[i]); 
        labels.push(months[i])
        data.push(response)
      }
    }
    setThreatChartData({
      labels:labels,
      datasets:[
        {
          label: "Peekers Spotted",
          fill: true,
          backgroundColor: 'rgba(23,232,10,0)',
          borderColor: "#00d6b4",
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          pointBackgroundColor: "#00d6b4",
          pointBorderColor: "rgba(255,255,255,0)",
          pointHoverBackgroundColor: "#00d6b4",
          pointBorderWidth: 20,
          pointHoverRadius: 4,
          pointHoverBorderWidth: 15,
          pointRadius: 4,
          data: data
        }
      ]
    })
  };

// !FOR THREAT CHART END

useEffect(()=>{
  if(state){
      updateActsList()
      actsChart()
      updateFilesList()
      filesChart()
      updateThreatList()
      threatChart()
  }
  return;
},[id])








  return (
    <>
      <div className="content content-fit">
        <Row>
          <Col xs="12">
            <Card className="card-chart">
              <CardHeader>
                <Row>
                  <Col className="text-left" sm="6">
                    <h5 className="card-category">Total Activity</h5>
                    <CardTitle tag="h2">Annually</CardTitle>
                  </Col>
                  <Col sm="6">
                  </Col>
                </Row>
              </CardHeader>
              <CardBody>
                <div className="chart-area">
                  <Line
                    data={actsChartData}
                    options={chartExample1.options}
                  />
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col lg="6">
            <Card className="card-chart">
              <CardHeader>
                <h5 className="card-category">Files Stored</h5>
                <CardTitle className="text-primary" tag="h5">
                  Files you stored
                </CardTitle>
              </CardHeader>
              <CardBody>
                <div className="chart-area">
                  <Bar
                    data={filesChartData}
                    options={chartExample3.options}
                  />
                </div>
              </CardBody>
            </Card>
          </Col>
          <Col lg="6">
            <Card className="card-chart">
              <CardHeader>
                <h5 className="card-category">Threats Evaded</h5>
                <CardTitle tag="h5" className="text-success">
                  People who tried to sneak a peak
                </CardTitle>
              </CardHeader>
              <CardBody>
                <div className="chart-area">
                  <Line
                    data={threatChartData}
                    options={chartExample4.options}
                  />
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

const mapStateToProps = state => ({
  state: state.auth.user
});

export default connect(mapStateToProps, null)(Dashboard);
