import React, {useState} from 'react';
import axios from 'axios';

const Support = () =>{

    const [formData, setFormData] = useState({
        contact_name : '',
        contact_email : '',
        contact_subject : '',
        contact_message  : ''
    });
    
    const { contact_name, contact_email, contact_subject, contact_message } = formData;
    
    const handleContactChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });
    
    const onContactSubmit = e => {
        e.preventDefault();
    
        contact_send(contact_name, contact_email, contact_subject, contact_message);
    };
    
    const contact_send = async (contact_name, contact_email, contact_subject, contact_message) => {
        const config={
            headers: {
                'Content-Type': 'application/json'
            }
        };
    
        const body = JSON.stringify({contact_name, contact_email, contact_subject, contact_message});
        console.log(body)
        try{
            const res = await axios.post(`http://fazeit.herokuapp.com/inquiry/`, body, config);
            console.log(res)
            if(res.status === 200){
                alert('done')
            }
        } catch (err) {
            console.log(err);
        }
    };


    return(
        <section id="support" className="container p-2 mt-2 mb-2">
            <div className="row p-3">
                <div className="col-sm-12 col-md-6 col-lg-6">
                    <div className="container mb-5">
                        <h2 className="display-2" style={{color: "#e14eac"}}>Reach us</h2>
                        <p>
                            It's very easy to get in touch with us. Just use the contact form or pay us a visit for a coffee at the office!
                        </p>
                        <br/>
                        <label style={{color:"white"}}>Head Office</label>
                        <address style={{color:"rgba(255, 255, 255, 0.8)"}}>
                            A-401, Ashoka Complex, ManickPur, Vasai(W),
                            Mumbai, IN - 401202
                        </address>

                        <label style={{color:"white"}}>Phone: </label>
                        <a href="tel:+918793656170" title="Give us a call!" target="_blank" rel="noopener noreferrer">&emsp;+91 8793656170</a>

                        <br/>

                        <label style={{color:"white"}}>E-Mail: </label>
                        <a href="mail-to:iyengar.abhi@gmail.com" title="Mail us your queries!" target="_blank" rel="noopener noreferrer">&emsp;ask@face.it</a>

                        <br/>
                        <br/>
                        <h4 className="display-4" style={{color: "#e14eca"}}>Find us on the map:</h4>
                        <iframe title="HOMap" id="HOMap" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1881.928686327926!2d72.82041802001496!3d19.375327456620262!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3be7ae968447bb47%3A0x64262c19cb5c71e1!2sAshoka%20Complex!5e0!3m2!1sen!2sin!4v1613558462021!5m2!1sen!2sin" frameBorder="0" allowFullScreen></iframe>
                    </div>
                </div>
                <div className="col-sm-12 col-md-6 col-lg-6">
                    <div className="container">
                        <div className="card p-4 bg-white">
                            <form id="ContactForm" onSubmit={e => onContactSubmit(e)}>
                                <div className="form-group">
                                    <label style={{color: "#333"}}>Name</label>
                                    <input 
                                        name="contact_name" 
                                        className="form-control" 
                                        type="text"
                                        value={contact_name} 
                                        maxLength="50"
                                        onChange={e=>handleContactChange(e)}
                                        placeholder="Tell us your name?"
                                    />
                                </div>
                                <div className="form-group">
                                    <label style={{color: "#333"}}>Email ID</label>
                                    <input 
                                        name="contact_email" 
                                        value={contact_email}
                                        onChange={e => handleContactChange(e)}
                                        type="email" 
                                        className="form-control" 
                                        placeholder="What's your e-mail?"
                                    />
                                    <small className="text-success">We never spam! Promise <i className="fas fa-heart" /></small>
                                </div>
                                <div className="form-group">
                                    <label style={{color: "#333"}}>Subject</label>
                                    <input 
                                        name="contact_subject" 
                                        value={contact_subject}
                                        onChange={e => handleContactChange(e)}
                                        type="text" 
                                        maxLength="100"
                                        className="form-control" 
                                        placeholder="What is this regarding?"
                                    />
                                </div>
                                <div className="form-group">
                                    <label style={{color: "#333"}}>Message</label>
                                    <textarea 
                                        name="contact_message" 
                                        style={{color: "#333"}}
                                        value={contact_message}
                                        onChange={e => handleContactChange(e)}
                                        minLength="20" 
                                        maxLength="200" 
                                        rows="10" 
                                        type="text" 
                                        className="form-control" 
                                        placeholder="What is this regarding?"
                                    ></textarea>
                                    <small className="text-info">Please keep it between 20-200 words!</small>
                                </div>
                                <br/>
                                <br/>
                                <div className="container-fluid text-center">
                                    <button id="contact_btn" title="Send message" type="submit" className="btn btn-primary">Send Message</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            
        </section>
    );
}

export default Support;