import React from 'react';
import fr from '../../assets/img/register.gif';
import rp from '../../assets/img/deletion.gif';
import enc from '../../assets/img/encryption.gif';
const Features = () =>{
    return(
        <section className="container p-2 mt-2 mb-2" id='features'>
            <h1 className="display-1">Features</h1>
            <span className="text-primary">Auth <span class='text-white'>~</span> Purge <span class='text-white'>~</span> Monitor </span>
            <div id="fr" className="mt-5">
                <div className="container-fluid row p-2">
                    <div className="col-sm-12 col-md-6 col-lg-6 text-white mt-2 order-second order-md-first">
                        <h2 className='display-2 text-success'>Face Auth</h2>
                        <article aria-label='Face Recognition based Authentication'>
                            Are you an avid smartphone user? Not habitual of typing in passwords there days are we?
                            Fret not! <strong>FaceIT</strong> uses your camera to log you in! Just tap the button and voila! You're signed it!
                            <br/><br/>
                            But wait! What if an imposter tries to sneak a peak at your data? <strong>FaceIT</strong> will capture their image and
                            the next time you log in, you can know exactly who has been snooping around on your data!
                            <br/><br/>
                            Got someone you'd rather share your data with? No problem! Just add them as <i>pseudousers</i> and they can use their very own credentials to authenticate themselves!
                            <br/><br/>
                        </article>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 p-3 text-center order-first order-md-second">
                        <img src={fr} alt="Face Recognition based Authentication" className='rounded-circle' width="320px" height="300px" />
                    </div>
                </div>
            </div>
            <div id="rp" className="mt-5">
                <div className="container-fluid row p-2">
                    <div className="col-sm-12 col-md-6 col-lg-6 p-3 text-center order-first order-md-first">
                        <img src={rp} alt="Remote purging of files"  className='rounded-circle' width="320px" height="300px" />
                    </div>
                    
                    <div className="col-sm-12 col-md-6 col-lg-6 mt-2 text-white order-second order-md-second">
                        <h2 className='display-2 text-success'>Remote Data Purging</h2>
                        <article aria-label='Remote file purging'>
                            <strong>FaceIT</strong>  can not only keep track of your files from your local system but also lets you delete them remotely at will!
                            <br/><br/>
                            How? you ask? It is as simple as clicking the delete button next to a file's name!
                             Next time your system comes back online, your file will be gone without a trace so you can rest easy.

                            <br/><br/>
                            Beware though, the files deleted in this manner <strong>CAN NOT</strong> be recovered! Make your desicions wisely!
                        </article>
                    </div>
                    
                </div>
            </div>
            <div id="enc" className="mt-5">
                <div className="continaer-fluid p-2 row">
                    <div className="col-sm-12 col-md-6 col-lg-6 mt-2 text-white order-second order-md-first">
                        <h2 className='display-2 text-success'>Golden Encryption</h2>
                        <article aria-label='Remote file purging'>
                            <strong>FaceIT</strong> is a file locker at the core. With <strong class="text-warning">Golden Encryption</strong> that boasts
                             custom made algorithms multiple layers of custom logical encryptions and SHA checksums you can rest assured that your data is safe
                              and no manipulation attempts go unnoticed!
                            <br/><br/>
                            What's more, <strong>FaceIT</strong> does not share your files or upload them to any servers. All of your data is stored
                            in your own local system, if you lose physical access to your system, you can always purge your files!
                            
                        </article>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 p-3 text-center order-first order-md-second">
                        <img src={enc}  className='rounded-circle' width="320px" height="300px" alt="Multilayer Encryption"  />
                    </div>
                </div>
            </div>
        </section>
    );
}


export default Features;