import React, {useState, useEffect, useRef} from 'react';
import {
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Row,
    Col
  } from "reactstrap";
import { connect } from 'react-redux';
import {toast} from 'react-toastify';
import axios from 'axios';

const Pseudousers = ({state}) => {
  //!Setting Ref constants
  let p_username = useRef(0);
  let p_firstName = useRef(0);
  let p_lastName = useRef(0);
  let p_pass = useRef(0);
  let p_repass = useRef(0);
  let p_pid = useRef(0);
  let p_btn = useRef();


  let s = {};
  if(state){
    s = state;
  }
  const id = s.id;
  console.log(id)
  useEffect(()=>{
    if(state){
      getPseudo();
    }
    return;
  },[id])
  const [pseudo, setPseudo] = useState([]);
  // const [addUser, setAddUser] = useState(false);
  // const [deleteUser, setDeleteUser] = useState(false);

  const getPseudo = async () =>{
    //setTimeout(()=>{}, 5000);
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': id
      }
    };
    try {
      const res =  await axios.get(`http://fazeit.herokuapp.com/pseudo/`, config);
      //const res =  await axios.get(`http://localhost:8000/pseudo/`, config);
      const populate = setPseudo(res.data.pseudoers);
      if(populate){
        return(200);
      }
      
    } catch (err) {
      console.log(err);
    }
  }

  //! Deleting pseudousers
  const deletePseudo = async (id) =>{
    var data = JSON.stringify({"id":parseInt(id)});

    var config = {
      method: 'delete',
      url: `http://fazeit.herokuapp.com/pseudo/`,
      headers: { 
        'Content-Type': 'application/json', 
      },
      data : data
    };

    axios(config)
    .then(function (response) {
      if(response.status === 204){
        getPseudo();
      }
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  //! Check for strong password
  function validatePassword(password) {
    var minNumberofChars = 6;
    var maxNumberofChars = 16;
    var regularExpression = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/;
    if(password.length < minNumberofChars || password.length > maxNumberofChars){
      return ("PassLenError");
    }
    if(!regularExpression.test(password)) {
      return ("PatternError");
    }else{
      console.log(password);
      return (201);
    }
  }

  //! Add Pseudousers
  const addPseudo = async(username,first_name,last_name,password,repassword, user=id) =>{
    let usernames = [];
    if(pseudo){
        let useLength = Object.keys(pseudo).length;
        if(useLength > 0){
            for(let i=0; i<useLength; i++){
                console.log(pseudo[i]);
                console.log(pseudo[i].username);
                usernames.push(pseudo[i].username);
            }
        }
        if(usernames.includes(username)){
            return("UsernameError");
        }
        if(password != repassword){
            return("PasswordsError");
        }else{
          const strength = validatePassword(password);
          if(strength === 201){
            const config = {
              headers: {
                  'Content-Type': 'application/json',
                  'Authorization': id
              }
            };
            const body = JSON.stringify({username, password, first_name, last_name, user})
            try{
                const res = await axios.post('http://fazeit.herokuapp.com/pseudo/', body, config);
                console.log(res);
                if(res.status === 201){
                    return(201);
                }else{
                    return(500);
                }
            }catch(error){
                console.log(error);
                return(500);
              }        
          }else{
            return strength;
          }
        }
       
    } 
  }

  //! Edit Pseudousers
  const editPseudo = async(p_id, username,first_name,last_name,password,repassword) =>{
    console.log(id, username,first_name,last_name,password,repassword)
    if(pseudo){
        if(password !== repassword){
            return("PasswordsError");
        }else{
          const strength = validatePassword(password);
          if(strength === 201){
            const config = {
              headers: {
                  'Content-Type': 'application/json',
                  'Authorization': p_id
                }
            };
            const user = id;
            const body = JSON.stringify({"id":p_id, username, password, first_name, last_name, user})
            try{
                const res = await axios.put('http://fazeit.herokuapp.com/pseudo/', body, config);
                console.log(res);
                if(res.status === 202){
                    return(201);
                }else{
                    return(500);
                }
            }catch(error){
                console.log(error);
                return(500);
            } 

          }else{
            return strength;
          }
        }    
    } 
  }

  const showEdit = (pid, username, first_name, last_name, password) =>{
    p_pid.current.innerHTML = pid;
    p_username.current.value = username;
    p_username.current.disabled = true;
    p_firstName.current.value = first_name;
    p_lastName.current.value = last_name;
    p_pass.current.value = password;
    p_repass.current.value = password;
    document.getElementById('p_btn').innerHTML = "Edit User";
  }

  //! Handling form data
  const handleSubmit = async(e) =>{
    e.preventDefault();
    let pid = p_pid.current.innerHTML;
    let username = p_username.current.value;
    let firstName = p_firstName.current.value;
    let lastName = p_lastName.current.value;
    let password = p_pass.current.value;
    let repassword = p_repass.current.value;
    let action = document.getElementById('p_btn').innerHTML;
    if(action === "Edit User"){
        const editUser = await editPseudo(pid, username,firstName,lastName,password,repassword);
        if(editUser === 201){
        //*Success
        toast.success("Pseudouser Edited Successfully!", {
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
        getPseudo();
        }else if(editUser === 500){
        //*Fail
        toast.error("Something went wrong! Try again later", {
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
        }else if(editUser === "PasswordsError"){
            toast.error("Your passwords do not match!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }else if(editUser === "UsernameError"){
            toast.error("Usernames must be unique!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }else if(editUser === "PassLenError"){
            toast.error("Passwords must be between 6 to 16 characters!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });

        }else if(editUser === "PatternError"){
            toast.error("Passwords must have atleast one special character and a number!", {
              position: "bottom-center",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });

        }

    }else{
        const addUser = await addPseudo(username,firstName,lastName,password,repassword);
        if(addUser === 201){
        //*Success
        toast.success("Pseudouser Added Successfully!", {
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
        getPseudo();
        }else if(addUser === 500){
        //*Fail
        toast.error("Something went wrong! Try again later", {
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
        }else if(addUser === "PasswordsError"){
            toast.error("Your passwords do not match!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }else if(addUser === "UsernameError"){
            toast.error("Usernames must be unique!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
          }else if(addUser === "PassLenError"){
            toast.error("Passwords must be between 6 to 16 characters!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });

        }else if(addUser === "PatternError"){
            toast.error("Passwords must have atleast one special character and a number!", {
              position: "bottom-center",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });

        }
        } 
      }
  return(
      <>
        <div className="content content-fit">
          <Row>
            <Col md="8">
              <Card>
                <CardHeader>
                  <h5 className="title">Manage PseudoUsers</h5>
                </CardHeader>
                <CardBody>
                  <form onSubmit={e=>handleSubmit(e)}>
                    <Row>
                      <Col md="12">
                        <span style={{visibility:"hidden"}} ref={p_pid}></span>
                        <FormGroup>
                          <label>Username</label>
                          <input
                            autoFocus
                            placeholder="Type in a cool username!"
                            className="form-control"
                            type="text"
                            name="username"
                            minLength="4"
                            maxLength="30"
                            required
                            ref={p_username}
                            style={{color: "white !important" }}
                          />
                          <div className="container">
                            <small className="pr-2 text-info">Keep it between 4-30 characters.</small>
                            <br/>
                            <small className="pr-2 text-warning">Special characters are not supported!</small>
                          </div>
                        </FormGroup>
                      </Col>
                    </Row>

                    <Row>
                      <Col md="6">
                        <FormGroup>
                          <label>First Name</label>
                          <input
                            placeholder="What's their first name?"
                            className="form-control"
                            type="text"
                            name="first_name"
                            maxLength="30"
                            required
                            ref={p_firstName}
                            style={{color: "white !important" }}
                          />
                        </FormGroup>
                      </Col>
                      <Col md="6">
                        <FormGroup>
                          <label>Last Name</label>
                          <input
                            placeholder="And their last name?"
                            className="form-control"
                            type="text"
                            name="last_name"
                            maxLength="30"
                            required
                            ref={p_lastName}
                            style={{color: "white !important" }}
                          />
                        </FormGroup>
                      </Col>
                    </Row>

                    <Row>
                      <Col md="6">
                        <FormGroup>
                          <label>Password</label>
                          <input
                            placeholder="Set a secret passphrase!"
                            className="form-control"
                            type="password"
                            name="password"
                            minLength='6'
                            maxLength='16'
                            required
                            ref={p_pass}
                            style={{color: "white !important" }}
                          />
                        </FormGroup>
                      </Col>
                      <Col md="6">
                        <FormGroup>
                          <label>Confirm Password</label>
                          <input
                            placeholder="Repeat the secret passphrase!"
                            className="form-control"
                            type="password"
                            name="re_password"
                            minLength='6'
                            maxLength='16'
                            required
                            ref={p_repass}
                            style={{color: "white !important" }}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <button className="btn btn-primary" ref={p_btn} color="primary" type="submit" id="p_btn">
                      Add User
                    </button>
                  </form>
                </CardBody>
              </Card>
            </Col>

            {/* DISPLAY PSEUDOUSERS SECTION */}
            <Col md="4">
              <Card >
                {
                  pseudo.length > 0 ?
                  pseudo.map((user,id) => (
                    
                    <CardBody key = {id}>
                      <div className="p-2 text-center d-flex justify-content-between container" style={{borderRadius: "4px", borderBottom: "1px solid #ff8d72"}}>
                        <h3 style={{textTransform: "uppercase", marginBottom: '0px'}}>{user.username}</h3>
                        <div>
                          <i
                            onClick={()=>{showEdit(user.id, user.username, user.first_name, user.last_name, user.password)}}
                            className={`tim-icons mb-1 icon-pencil bg-info text-white p-2 rounded-circle`}
                          />
                          &emsp;
                          <i 
                            onClick={()=>{deletePseudo(user.id)}} 
                            className={`tim-icons mb-1 icon-trash-simple bg-danger text-white p-2 rounded-circle`}
                          />
                        </div>
                      </div>
                    </CardBody>
                  )):(
                    <CardBody>
                      <div className="container px-3 py-5 mt-2 text-center">
                        <h4>You have no <strong className="text-primary">Pseudousers</strong></h4>
                      </div>
                    </CardBody>
                  )
                }
              </Card>
            </Col>
          </Row>
        </div>
      </>
  );
}

const mapStateToProps = state => ({
  state: state.auth.user
});

export default connect(mapStateToProps, null)(Pseudousers);