import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password_confirm } from '../../actions/auth';
import axios from 'axios';

const ForgotPasswordConfirm = ({ reset_password_confirm, match }) => {

    const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        new_password: '',
        re_new_password: ''
    });

    const { new_password, re_new_password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
      e.preventDefault();
      let url_params = window.location.href.split('/')
      console.log(url_params)
      const uid = url_params[7];
      const token = url_params[8];
      console.log(uid, token);
      reset_password_confirm_local(uid, token, new_password, re_new_password);
      //setRequestSent(true);
    };

    // if (requestSent) {
    //     return <Redirect to='/' />
    // }

    const reset_password_confirm_local = async (uid, token, new_password, re_new_password) => {
      const config = {
          headers: {
              'Content-Type': 'application/json'
          }
      };

      const body = JSON.stringify({ uid, token, new_password, re_new_password });
      console.log(body)

      try {
        // const res =  await axios.post(`http://127.0.0.1:8000/auth/users/reset_password_confirm/`, body, config);
        const res =  await axios.post(`http://fazeit.herokuapp.com/auth/users/reset_password_confirm/`, body, config);
        console.log(res)
        if(res.status === 204)
        {
          return <Redirect to="common/login/" />
        }
      } catch (err) {
        console.log(err);
        
      }
    };

  return (
    <div className="content">
      <div className="ForgotMain">
        <div className="container-fluid row">
          <div className="col-sm-12 col-lg-7 col-md-7">
            <div id='spacer20'></div><div id='spacer10'></div>
            <div className="container p-4 col-sm-12 col-md-10 col-lg-10">
              <br/>
              <br/>
              <h1 className="display-1 text-white">Reset Password</h1>
              <div className="col-sm-12 pt-2 col-md-10 col-lg-10">
                <h4>Choose a brand new passpharse!
                  <br/>
                  <h6>And remember it this time!</h6>
                </h4>
              </div>
            </div>
          </div>
          <div className="col-sm-12 col-lg-5 col-md-5">
            <div id='spacer20'></div>
            <div className="container-fluid">
              <div className="card p-4 col-sm-12 col-md-10 col-lg-10 bg-white">
                <div className="p-2">
                  <h4 className="display-4" style={{color: "#e14eac"}}>Reset Password</h4>
                  <p className="text-muted">Enter your email to get a password reset link.</p>
                </div>
                <div className="card-body pt-4">
                  <form id="FPForm" onSubmit={e => onSubmit(e)}>
                    <div className="form-group">
                      <label for="email_id">New Password</label>
                      <input 
                        className="form-control" 
                        placeholder="Choose a new passpharse" 
                        type="password" 
                        name='new_password'
                        minLength='6'
                        required
                        value={new_password}
                        style={{color: "#333"}}
                        onChange={e => onChange(e)}
                      />
                    </div>

                    <div className="form-group">
                      <label for="email_id">Confirm Password</label>
                      <input 
                        className="form-control" 
                        placeholder="Confirm your new passpharse" 
                        type="password"
                        name='re_new_password'
                        value={re_new_password}
                        minLength='6'
                        required
                        style={{color: "#333"}}
                        onChange={e => onChange(e)}
                      />
                    </div>
                    <br/>
                    <br/>
                    <div className="contianer text-center">
                      <button className="btn btn-primary" type='submit' id="log_btn">Reset</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  ); 
}

export default connect(null, { reset_password_confirm })(ForgotPasswordConfirm);
