import React  from 'react';
import d_img from '../assets/img/2fa.svg';
const Companion = () =>{
    //Detect OS
    function DetectOS(){
        var Detect = {
            init: function () {
                this.OS = this.searchString(this.dataOS);
            },
            searchString: function (data) {
                for (var i=0;i<data.length;i++) {
                    var dataString = data[i].string;
                    var dataProp = data[i].prop;
                    if (dataString) {
                        if (dataString.indexOf(data[i].subString) !== -1)
                            return data[i].identity;
                    }
                    else if (dataProp)
                        return data[i].identity;
                }
            },
            dataOS : [
                {
                    string: navigator.platform,
                    subString: 'Win',
                    identity: 'Windows'
                },
                {
                    string: navigator.platform,
                    subString: 'Mac',
                    identity: 'macOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'iPhone',
                    identity: 'iOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'iPad',
                    identity: 'iOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'iPod',
                    identity: 'iOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'Android',
                    identity: 'Android'
                },
                {
                    string: navigator.platform,
                    subString: 'Linux',
                    identity: 'Linux'
                }
            ]
        };
    
        Detect.init();
        return(Detect.OS);
    }
    return(
        <section className="content content-fit">
            <div className="container text-center p-3">
                <img src={d_img} height="340px" width="340px" alt="Download FaceIT now!"/> 
                <div className="container mt-2">
                    <h1 className="display-2 text-primary">Need the Companion Apps?</h1>
                    <h3 className="display-4 text-warning">We can help you with that!</h3>

                    {
                        DetectOS() === "Windows" &&
                        <a className="btn btn-primary" href="https://raw.githubusercontent.com/tinshade/faceit/main/download/faceit.rarDownload Windows" title="Download FaceIT for Windows 10 - 64bit">Download For Windows</a>
                    }
                    {
                        DetectOS() === "macOS" &&
                        <a className="btn btn-primary" href="https://raw.githubusercontent.com/tinshade/faceit/main/download/faceit.rarDownload macOS" title="Download FaceIT for macOS - 64bit">Download For macOS</a>
                    }
                    {
                        DetectOS() === "Linux" &&
                        <a className="btn btn-primary" href="https://raw.githubusercontent.com/tinshade/faceit/main/download/faceit.rarDownload Linux" title="Download FaceIT for Linux - 64bit">Download For Linux</a>
                    }
                    <br/>
                    <br/>
                    <br/>
                    <hr/>
                    <br/>
                    
                    <div>
                        <h4>Need the Companion Apps?</h4>
                        <h5>Monitor your data, wherever you go!</h5>
                        <a className="btn btn-success" title="Download for Android 7 and above" href="https://raw.githubusercontent.com/tinshade/faceit/main/download/faceit.apk"><i className="fab fa-android fa-2x"></i></
                        a>
                        <a className="btn btn-info disabled" title="iOS version coming soon!" href="https://raw.githubusercontent.com/tinshade/faceit/main/download/faceit.apk"><i className="fab fa-apple fa-2x"></i></
                        a>
                    </div>
                </div>
            </div>
        </section>
    );
}


export default Companion;