import React, { useEffect } from 'react';
import { Card, CardBody } from 'reactstrap';
import { Link, useLocation } from 'react-router-dom';
import { connect } from 'react-redux';
import { googleAuthenticate } from '../../actions/auth';
import queryString from 'query-string';

const Google = ({ googleAuthenticate }) => {
    let location = useLocation();

    useEffect(() => {
        const values = queryString.parse(location.search);
        const state = values.state ? values.state : null;
        const code = values.code ? values.code : null;

        console.log('State: ' + state);
        console.log('Code: ' + code);

        if (state && code) {
            googleAuthenticate(state, code);
        }
    }, [location]);

    return (
        <div className='content' style={{marginTop: "15%"}}>
            <div className="text-center col-sm-12 col-md-4 col-lg-4 p-2 container">
                <Card>
                    <div className="p-5">
                        <CardBody>
                            <h2 className='text-primary'>Great Job!</h2>
                            <h4 className='text-success'>You've authenticated using Google!</h4>
                            <p>Try logging in with the same account!</p>
                            <br/>
                            <Link className='btn btn-primary' to='/login'>Login</Link>
                        </CardBody>
                    </div>
                </Card>
            </div>
        </div>
    );
};

export default connect(null, { googleAuthenticate })(Google);
