import React, {useState} from 'react';
import {Card, Row} from 'reactstrap';
import {ToastContainer, toast} from 'react-toastify';
import axios from "axios";
import { connect } from 'react-redux';
const Files = ({state}) =>{
    let s = {};
    if(state){
        s = state;
    }
    const id = s.id;
    const [filez, setFilez] = useState([]);
    const getFiles = async() =>{
        const config = {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': id
            }
        };
        try{
            const res =  await axios.get(`${process.env.REACT_APP_API_URL}/filez/`, config);
            setFilez(res.data)
        }catch(e){
            toast.error("Something went wrong! Try again later!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            console.log(e)
        }

        
    }

    useState(()=>{
        if(id){
            getFiles();
        }
    },[filez])


    const schedulePurge = async(file_id) =>{
        const config={
            headers:{
                'Content-Type': 'application/json',
                'Authorization': id,
            }
        }
        const purge_scheduled = true;
        const body = JSON.stringify({file_id, purge_scheduled})
        try{
            const res = await axios.put(`${process.env.REACT_APP_API_URL}/filez/`, body, config);
            console.log(res.data);
            if(res.status === 200){
                console.log(res.status);
                console.log("Done")
                //*Success
                toast.success("Your file is scheduled to be purged!", {
                    position: "bottom-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }catch(e){
            //*Failed
            toast.error("Something went wrong! Try again later!", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    }

    return(
        <div className="content content-fit">
            <Row className="mt-2 mx-auto">
                <Card className="p-2">
                    <div className="table-responsive">
                        <table className="table table-hover table-dark text-center">
                            <thead>
                                <tr>
                                <th scope="col">#</th>
                                <th scope="col">Name</th>
                                <th scope="col">Size</th>
                                <th scope="col">Extension</th>
                                <th scope="col">Status</th>
                                <th scope="col">Tier</th>
                                <th scope="col">Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    filez.length>0?
                                    filez.map((file,id) => (
                                        <tr key={id}>
                                            <th scope="row" style={{color: "white"}}>{id+1}</th>
                                            <td style={{textTransform:"capitalize"}}>{file.file_name}</td>
                                            <td>{file.file_size}KB</td>
                                            <td style={{textTransform:"uppercase"}}>{file.file_ext}</td>
                                            <td>
                                                {
                                                    file.isDecrypted ? <span className="text-danger uppercase">Decrypted</span> : <span className="text-primary uppercase">Encrypted</span>
                                                }
                                            </td>
                                            <td style={{textTransform:"uppercase"}}>
                                            {
                                                file.enc_level === '1'
                                                &&
                                                <span className="text-warning">Low</span>
                                            }
                                            {
                                                file.enc_level === '2'
                                                &&
                                                <span className="text-info">Medium</span>
                                            }
                                            {
                                                file.enc_level === '3'
                                                &&
                                                <span className="text-success">High</span>
                                            }
                                            </td>
                                            <td>
                                                <button 
                                                    className="btn btn-danger"
                                                    onClick={()=>{schedulePurge(file.file_id)}}
                                                >
                                                    <i className="fas fa-trash" />
                                                </button>
                                            </td>
                                        </tr>
                                    ))
                                    :(
                                        <tr className="text-center">
                                            <td colSpan="7">ITS ALL SO EMPTY <i className="fas fa-frown"/></td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>
                    </div>
                </Card>
            </Row>
            <ToastContainer />
        </div>
    );
}


const mapStateToProps = state => ({
    state: state.auth.user
  });
  
export default connect(mapStateToProps, null)(Files);