import React, {useState, useEffect} from "react";
import {Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from '../../actions/auth';


const Login = ({login, isAuthenticated}) => {
  const [quote,setQuote] = useState([]);
  useEffect(() => {
    loadQuote();
  }, []);


  const loadQuote = async() =>{
    const url = 'https://type.fit/api/quotes';
    const response = await fetch(url);
    const data = await response.json();
    setQuote(data[Math.floor((Math.random() * 1600) + 1)]);
  }


  const [formData, setFormData] = useState({
      email: '',
      password: '' 
  });

  const { email, password } = formData;

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = e => {
      e.preventDefault();

      login(email, password);
  };

  if (isAuthenticated) {
      return <Redirect to='/' />
  }

  return (
    <div className="content">
      <div className="LoginMain">
        <div className="container-fluid row">
          <div className="col-sm-12 col-lg-7 col-md-7">
            <div id='spacer20'></div><div id='spacer20'></div>
            <div className="container p-4 col-sm-12 col-md-10 col-lg-10">
              <h2 className="display-1 text-white">Welcome Back!</h2>
              <div className="col-sm-12 col-md-10 col-lg-10">
                {
                  quote.text === "" || quote.author === ""
                }
                <></>
                {
                    <h4 id="qotd">{quote.text}
                        <br/>
                        <h6>~{quote.author}</h6>
                    </h4>
                }
              </div>
            </div>
          </div>
          <div className="col-sm-12 col-lg-5 col-md-5">
            <div id='spacer20'></div>
            <div className="container-fluid">
              <div className="card p-4 col-sm-12 col-md-10 col-lg-10 bg-white">
                <div className="p-2">
                  <h4 className="display-4" style={{color: "#e14eac"}}>Login</h4>
                  <p className="text-muted">Sign in to continue.</p>
                </div>
                <div className="card-body pt-4">
                  <form id="LoginForm" onSubmit={e => onSubmit(e)}>
                    <div className="form-group">
                      <label style={{color: "#333"}} htmlFor="email_id">Email Address</label>
                      <input 
                        required 
                        aria-required 
                        autoFocus 
                        className="form-control" placeholder="Your registerd email ID" 
                        type="email" 
                        id="email"
                        name="email"
                        value={email}
                        style={{color: "#333"}}
                        onChange={e => onChange(e)}
                      />
                    </div>
                    <div className="form-group">
                      <label style={{color: "#333"}} htmlFor="password">Password</label>
                      <input 
                        aria-required 
                        required 
                        className="form-control" placeholder="Your password" type="password" 
                        id="password"
                        name="password"
                        minLength='6'
                        value={password}
                        style={{color: "#333"}}
                        onChange={e => onChange(e)}
                      />
                    </div>
                    <div className="form-group float-right">
                      <small className="float-right"><Link className="text-muted" target="_blank" to="/common/forgot_password/" title="Forgot your password? Worry not!">Forgot Password?</Link></small>
                    </div>
                    <br/>
                    <br/>
                    <div className="contianer text-center">
                      <button type="submit" className="btn btn-primary" id="log_btn">Login</button>
                    </div>
                  </form>
                </div>
                <hr/>
                <div className="card-footer pt-2">
                  <small style={{color:"#333"}}>Don't have an account? <Link to="/common/register/" title="Sign up with FaceIT!">Create one!</Link></small>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  ); 
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { login })(Login);
