import React, {useEffect, useRef} from "react";
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardText,
  FormGroup,
  Row,
  Col
} from "reactstrap";
import {toast} from 'react-toastify';
import { connect } from 'react-redux';
import axios from 'axios';

const UserProfile = ({state}) => {

  let user = {};
  if(state){
    user = state;
  }
  const id = user.id;
  

  
  const email_ref = useRef(0);
  const first_name_ref = useRef(0);
  const first_name_side = useRef(0);
  const last_name_ref = useRef(0);
  const last_name_side = useRef(0);
  const address_ref = useRef(0);
  const phone_ref = useRef(0);
  const city_ref = useRef(0);
  const country_ref = useRef(0);
  const postal_code_ref = useRef(0);
  const bio_ref = useRef(0);
  const bio_side = useRef(0);

  let plan;


  //! Getting the user profile
  const getUserDetails = async () => {
    const config = {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': id
        }
    };
    try {
      const res =  await axios.get(`${process.env.REACT_APP_API_URL}/accounts/profile/`, config);
      //Populating input fileds
      email_ref.current.value = res.data.user_details.email
      first_name_ref.current.value = res.data.user_details.first_name
      first_name_side.current.innerHTML = res.data.user_details.first_name
      last_name_ref.current.value = res.data.user_details.last_name
      last_name_side.current.innerHTML = res.data.user_details.last_name
      address_ref.current.value = res.data.profile.address
      phone_ref.current.value = res.data.profile.phone
      city_ref.current.value = res.data.profile.city
      country_ref.current.value = res.data.profile.country
      postal_code_ref.current.value = res.data.profile.postal_code
      bio_ref.current.value = res.data.profile.bio
      bio_side.current.innerHTML = res.data.profile.bio
      plan = res.data.profile.plan      
      
    } catch (err) {
      console.log(err);
    }
  };
  //getUserDetails();

  useEffect(()=>{
    getUserDetails();
    return;
  },[id])
  //!Handle Submit
  const onSubmit = e => {
    e.preventDefault();
    updateUserProfile();
  };

  const updateUserProfile = async() => {
    var data = JSON.stringify({"phone":phone_ref.current.value,"address":address_ref.current.value,"city":city_ref.current.value,"country":country_ref.current.value,"postal_code":parseInt(postal_code_ref.current.value),"bio":bio_ref.current.value,"plan":plan});

    var config = {
      method: 'put',
      url: `${process.env.REACT_APP_API_URL}/accounts/profile/`,
      headers: { 
        'Content-Type': 'application/json', 
        'Authorization': id
      },
      data : data
    };

    axios(config)
    .then(function (response) {
      if(response.status === 201){
        toast.success("Details updated!", {
          position: "bottom-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        getUserDetails();
      }
    })
    .catch(function (error) {
      console.log(error);
      toast.error("Something went wrong! Try again later!", {
        position: "bottom-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    });
  }



  return(
    <>
      <div className="content content-fit">
        <Row>
          <Col md="8">
            <Card>
              <CardHeader>
                <h5 className="title">Edit Profile</h5>
              </CardHeader>
              <CardBody>
                <form onSubmit={e=>onSubmit(e)}>
                  <Row>
                    <Col  md="4">
                      <FormGroup>
                        <label htmlFor="exampleInputEmail1">
                          Email address
                        </label>
                        <input 
                          placeholder="abc@domain.com" 
                          type="email"
                          name="email"
                          ref={email_ref}
                          required
                          disabled
                          style={{color: "white !important"}}
                          className="form-control"
                        />
                      </FormGroup>
                    </Col>
                    <Col md="4">
                      <FormGroup>
                        <label>First Name</label>
                        <input
                          placeholder="First Name"
                          type="text"
                          name="first_name"
                          ref={first_name_ref}
                          required
                          disabled
                          style={{color: "white !important"}}
                          className="form-control"
                        />
                      </FormGroup>
                    </Col>
                    <Col md="4">
                      <FormGroup>
                        <label>Last Name</label>
                        <input
                          placeholder="Last Name"
                          type="text"
                          name="last_name"
                          ref={last_name_ref}
                          required
                          disabled
                          style={{color: "white !important"}}
                          className="form-control"
                        />
                      </FormGroup>
                    </Col>
                    
                  </Row>
                  <Row>
                    <Col md="12">
                      <FormGroup>
                        <label>Address</label>
                        <input
                          className="form-control"
                          placeholder="Home Address"
                          type="text"
                          maxLength="200"
                          name="address"
                          ref={address_ref}
                          style={{color: "white !important" }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="6">
                      <FormGroup>
                          <label>Phone</label>
                          <input 
                            type="text"
                            ref={phone_ref}
                            name="phone"
                            maxLength="10"
                            className="form-control"
                          />
                        </FormGroup>
                    </Col>
                    <Col md="6">
                      <FormGroup>
                        <label>City</label>
                        <input
                          placeholder="City"
                          className="form-control"
                          type="text"
                          name="city"
                          ref={city_ref}
                          style={{color: "white !important" }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    
                    <Col md="6">
                      <FormGroup>
                        <label>Country</label>
                        <input
                          placeholder="Country"
                          className="form-control"
                          type="text"
                          name="country"
                          ref={country_ref}
                          style={{color: "white !important" }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md="6">
                      <FormGroup>
                        <label>Postal Code</label>
                        <input 
                          placeholder="ZIP Code" 
                          className="form-control"
                          type="text" 
                          maxLength="6"
                          name="postal_code"
                          ref={postal_code_ref}
                          style={{color: "white !important" }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="12">
                      <FormGroup>
                        <label>About Me</label>
                        <textarea
                          cols="80"
                          className="form-control"
                          placeholder="Here can be your description"
                          rows="4"
                          maxLength="200"
                          name="bio"
                          ref={bio_ref}
                          style={{color: "white !important" }}
                        ></textarea>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button className="btn-fill" color="primary" type="submit">
                    Save
                  </Button>
                </form>
              </CardBody>
            </Card>
          </Col>
          <Col md="4">
            <Card className="card-user">
              <CardBody>
                <div className="author">
                  {/* <div className="block block-one" />
                  <div className="block block-two" />
                  <div className="block block-three" />
                  <div className="block block-four" /> */}
                  <a href="#pablo" onClick={e => e.preventDefault()}>
                    <img
                      alt="..."
                      className="avatar border border-primary"

                      src="https://raw.githubusercontent.com/tinshade/faceit/main/media/User_Images/default.gif"
                    />
                    <h5 className="title">
                      <span style={{textTransform:"capitalize"}} ref={first_name_side}></span>
                      &nbsp;
                      <span style={{textTransform:"capitalize"}} ref={last_name_side}></span>
                    </h5>
                  </a>
                  <p className="description">Primary User</p>
                </div>
                <div className="card-description" ref={bio_side}>
                </div>
              </CardBody>
              <CardFooter>
                {/* <div className="button-container">
                  <Button className="btn-icon btn-round" color="facebook">
                    <i className="fab fa-facebook" />
                  </Button>
                  <Button className="btn-icon btn-round" color="twitter">
                    <i className="fab fa-twitter" />
                  </Button>
                  <Button className="btn-icon btn-round" color="google">
                    <i className="fab fa-google-plus" />
                  </Button>
                </div> */}
              </CardFooter>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

const mapStateToProps = state => ({
  state: state.auth.user
});

export default connect(mapStateToProps, null)(UserProfile);
