import React from 'react';
import d_img from '../../assets/img/2fa.svg';
import d_img1 from '../../assets/img/2fa1.svg';
import d_img2 from '../../assets/img/2fa2.svg';
import d_img3 from '../../assets/img/2fa3.svg';
import d_img4 from '../../assets/img/2fa4.svg';

const Download  = () =>{

    var clients = [
        { 
         logo: {d_img}
        },
      
        {
          logo: {d_img1}
        },
        {
          logo: {d_img2}
        },
        { 
          logo: {d_img3}
        },
        {
          logo: {d_img4}
        }
      ];
      
      function repeatImage(){
      
      let result = clients.slice(0, 4).map(function () { 
              return this.splice(Math.floor(Math.random() * this.length), 1)[0];
          }, clients.slice());
      
      
            var firstOnScreen = result[0];
            document.getElementById("fafa").src = firstOnScreen.logo;
      
            var secondOnScreen = result[1];
            document.getElementById("fafa").src = secondOnScreen.logo;
      
            var thirdOnScreen = result[2];
            document.getElementById("fafa").src = thirdOnScreen.logo;
      
            var fourthOnScreen = result[3];
            document.getElementById("fafa").src = fourthOnScreen.logo;
      };

      //setInterval( repeatImage, 2000);
    //Detect OS
    function DetectOS(){
        var Detect = {
            init: function () {
                this.OS = this.searchString(this.dataOS);
            },
            searchString: function (data) {
                for (var i=0;i<data.length;i++) {
                    var dataString = data[i].string;
                    var dataProp = data[i].prop;
                    if (dataString) {
                        if (dataString.indexOf(data[i].subString) !== -1)
                            return data[i].identity;
                    }
                    else if (dataProp)
                        return data[i].identity;
                }
            },
            dataOS : [
                {
                    string: navigator.platform,
                    subString: 'Win',
                    identity: 'Windows'
                },
                {
                    string: navigator.platform,
                    subString: 'Mac',
                    identity: 'macOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'iPhone',
                    identity: 'iOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'iPad',
                    identity: 'iOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'iPod',
                    identity: 'iOS'
                },
                {
                    string: navigator.userAgent,
                    subString: 'Android',
                    identity: 'Android'
                },
                {
                    string: navigator.platform,
                    subString: 'Linux',
                    identity: 'Linux'
                }
            ]
        };
    
        Detect.init();
        return(Detect.OS);
    }

    return(
        <section className="container p-2 mt-2 mb-2" id="downloads">
            <div className="container row p-3">
                <div className="col-sm-12 col-md-5 col-lg-5 order-last order-md-first">
                    <h1 className="display-2">Don't have the suite yet?</h1>
                    <h3 className="display-4">We can help you with that!</h3>
                    
                    {
                        DetectOS() === "Windows" &&
                        <a className="btn btn-primary" href="Download For Windows" title="Download FaceIT for Windows 10 - 64bit">Download For Windows</a>
                    }
                    {
                        DetectOS() === "macOS" &&
                        <a className="btn btn-primary" href="Download For macOS" title="Download FaceIT for macOS - 64bit">Download For macOS</a>
                    }
                    {
                        DetectOS() === "Linux" &&
                        <a className="btn btn-primary" href="Download For Linux" title="Download FaceIT for Linux - 64bit">Download For Linux</a>
                    }{
                        DetectOS() === "Android" &&
                        <a className="btn btn-primary" href="drive link to android app" title="Download FaceIT for Android 7 and above">Download For Android</a>
                    }
                    <br/>
                    <br/>
                    <br/>
                    <hr/>
                    <br/>
                    <div>
                        <h4>Need the Companion Apps?</h4>
                        <h5>Monitor your data, wherever you go!</h5>
                        <a className="btn btn-success"  title="Download FaceIT for Android 7 and above" href="drive link to android app"><i className="fab fa-android fa-2x"></i></
                        a>
                        <a className="btn btn-info disabled" title="Coming soon!" href="download/ios"><i className="fab fa-apple fa-2x"></i></
                        a>
                    </div>
                </div>
                <div className="col-sm-12 col-md-1 col-lg-1"></div>
                <div className="col-sm-12 col-md-6 col-lg-6 order-first order-md-last mb-5">
                    <img src={d_img} className="img-fluid"  alt="Download FaceIT now!"/> 
                </div>
            </div>
        </section>
    );
}

export default Download;