import React, {useState, useEffect, useRef} from 'react';
import {Card, CardBody,Row, Col, CardFooter, Button, FormGroup} from 'reactstrap';
import axios from 'axios';
import { connect } from 'react-redux';
import {toast} from 'react-toastify';
const Settings = ({state, auth}) =>{
    let id;
    if(state){
        id = state.id;
    }

    //! Refs section
    let form_old_password = useRef();
    let form_new_password = useRef();
    let form_re_new_password = useRef();

    
    const checkPassword = async(old_password) =>{
        const config = {
            headers:{
                "Content-Type": "application/json",
                "Authorization" : id
            }
        }
        const body = JSON.stringify({old_password});
        try{
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/accounts/verify/`, body, config);
            console.log(res);
            if(res.status === 200){
                return 200;
            }else if(res.status===203){
                return 203;
            }
        }catch(e){
            console.log(e);
            return 500;
        }
    }

    //! Check for strong password
    function validatePassword(password) {
        var minNumberofChars = 6;
        var maxNumberofChars = 16;
        var regularExpression = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/;
        if(password.length < minNumberofChars || password.length > maxNumberofChars){
        return ("PassLenError");
        }
        if(!regularExpression.test(password)) {
        return ("PatternError");
        }else{
            return (201);
        }
    }

    const changePassword = async(current_password, new_password, re_new_password) => {
        const config = {
            headers:{
                "Content-Type": "application/json",
                "Authorization" : `JWT ${auth.access}`
            }
        }
        const body = JSON.stringify({current_password, new_password, re_new_password});
        try{
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/users/set_password/`, body, config);
            if(res.status === 200){
                return 200;
            }
        }catch(e){
            console.log(e);
            return 500;
        }
    }

    const generateFile = async(password) => {
        const email = state.email;
        const config={
            headers:{
                "Content-Type": "application/json"
            }
        }
        const body = JSON.stringify({email, password});
        try{
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/accounts/generate/`, body, config);
            console.log(res);
            if(res.status === 201){
                return 201;
            }else{
                return 500;
            }
        }catch(err){
            console.log(err)
            return 500;
        }
        

    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        if(form_new_password.current.value === form_re_new_password.current.value){
            let strength = validatePassword(form_new_password.current.value);
            if(strength === 201){
                let verify = await checkPassword(form_old_password.current.value);
                console.log(verify)
                if(verify === 200){
                    if(await generateFile(form_new_password.current.value) === 201){
                        if(await changePassword(form_old_password.current.value, form_new_password.current.value,form_re_new_password.current.value) === 200){
                            toast.success("Password changed successfully!", {
                                position: "bottom-center",
                                autoClose: 5000,
                                hideProgressBar: false,
                                closeOnClick: true,
                                pauseOnHover: true,
                                draggable: true,
                                progress: undefined,
                              });
                        }else{
                            console.log("Something went wrong! Try again later");
                            toast.error("Something went wrong! Try again later!", {
                                position: "bottom-center",
                                autoClose: 5000,
                                hideProgressBar: false,
                                closeOnClick: true,
                                pauseOnHover: true,
                                draggable: true,
                                progress: undefined,
                              });  
                        }
                    }else{
                        console.log("Something went wrong! Try again later");
                        toast.error("Something went wrong! Try again later!", {
                            position: "bottom-center",
                            autoClose: 5000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                          });
                    }
                }else if(verify===203){
                    console.log("This is not your old password");
                    toast.error("Did you mistype something?", {
                        position: "bottom-center",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                      });
                }
            }else{
                console.log(strength);
            }
        }else{
            console.log("Passwords dont match");
            toast.error("Did you mistype something?", {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
        }
    }

    function deactivateAccount(){
        console.log("deactivateAccount"); //Set active to 0
    }
    function deleteAccount(){
        console.log("deleteAccount"); //Delete account
    }




    return(
        <div className="content content-fit">
            <Card style={{margin:"10px 0px" }} className="p-2">
                
                <form onSubmit={e => handleSubmit(e)}>
                    <CardBody className="p-4">
                    <h4>SECURITY MANAGEMENT</h4>
                    <small>It is highly recommended to change your password every 3 months. We know it's a pain to rememeber all your super strong passwords but try not to save them anywhere to stay protected from any data leaks!</small>
                        <Row className="mt-2 p-2">
                            <Col>
                                <FormGroup>
                                    <label htmlFor="exampleInputEmail1">
                                        Current Password
                                    </label>
                                    <input 
                                        placeholder="Your current password" 
                                        type="password"
                                        name="old_password"
                                        ref={form_old_password}
                                        required
                                        style={{color: "white !important"}}
                                        className="form-control"
                                    />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row className="p-2">
                            <Col md="6">
                                <FormGroup>
                                    <label htmlFor="exampleInputEmail1">
                                        New Password
                                    </label>
                                    <input 
                                        placeholder="Your current password" 
                                        type="password"
                                        name="new_password"
                                        ref={form_new_password}
                                        required
                                        style={{color: "white !important"}}
                                        className="form-control"
                                    />
                                </FormGroup>
                            </Col>
                            <Col md="6">
                                <FormGroup>
                                    <label htmlFor="exampleInputEmail1">
                                        Confirm Password
                                    </label>
                                    <input 
                                        placeholder="Your current password" 
                                        type="password"
                                        name="re_new_password"
                                        ref={form_re_new_password}
                                        required
                                        style={{color: "white !important"}}
                                        className="form-control"
                                    />
                                </FormGroup>
                            </Col>
                        </Row>
                    </CardBody>
                    <CardFooter>
                        <Button className="btn btn-info" type="submit">Update</Button>
                    </CardFooter>
                </form>
            </Card>
            <br/><br/>
            <h1 className="text-danger">Danger Section</h1>
            {/* DELETE ACCOUNT SECTION */}
            <Card style={{margin:"10px 0px" }} classname="p-2">
                <CardBody className="p-4">
                    <h4>ACCOUNT MANAGEMENT</h4>
                    <small>Please use this section with caution. There will be no confirmation dialogs and the actions will be permanent!</small>
                    <Row className="mt-2 p-4">
                        <Col md="6" style={{borderRight: '1px solid rgba(255,255,255,0.4)'}}>
                            <div className="container-fluid p-4 my-3 text-center" style={{borderRadius:"7px"}}>
                                <p>I want to deactivate my account for a while. I will be back!</p>
                                <button onClick={()=>{deactivateAccount()}} className="btn btn-warning">DEACTIVATE</button>
                            </div>
                        </Col>
                        <Col md="6" style={{borderLeft: '1px solid rgba(255,255,255,0.4)'}}>
                            <div className="container-fluid p-4 my-3 text-center" style={{borderRadius:"7px"}}>
                                <p>I want to delete my account permanently. I will not return!</p>
                                <button onClick={()=>{deleteAccount()}} className="btn btn-danger">DELETE</button>
                            </div>
                        </Col>
                    </Row>
                </CardBody>
                
            </Card>

        </div>
    );
}

const mapStateToProps = state => ({
    auth: state.auth,
    state: state.auth.user
  });
  
export default connect(mapStateToProps, null)(Settings);