import React, {useState} from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { verify } from '../../actions/auth';


const Activate = ({verify}) => {
    const [verified, setVerified] = useState(false);

    const verify_account = () => {

        const url = String(window.location.href).split('/activate/')
        const uid = url[1].split('/')[0];
        const token = url[1].split('/')[1];

        verify(uid, token);
        setVerified(true);
    };

    if (verified) {
        return <Redirect to='/common/login' />
    }
    return(
        <div className="content">
            <div className="LoginMain">
                <div className="container-fluid row">
                <div className="col-sm-12 col-lg-7 col-md-7">
                    <div id='spacer20'></div>
                    <div className="container p-4 col-sm-12 col-md-10 col-lg-10">
                    <h2 className="display-1 text-white">Hey there!</h2>
                    <div className="col-sm-12 col-md-10 col-lg-10">
                        <h4 id="qotd">Looking to activate your account?
                            <br/>
                            <h6>Just a tap away!</h6>
                        </h4>
                    </div>
                    </div>
                </div>
                <div className="col-sm-12 col-lg-5 col-md-5">
                    <div id='spacer20'></div>
                    <div className="container-fluid">
                        <div className="card p-4 col-sm-12 col-md-10 col-lg-10 bg-white">
                            <div className="p-2 text-center">
                                <h4 className="display-4" style={{color: "#e14eac"}}>Activate</h4>
                                <p className="text-muted">Tap <strong className="text-primary">verify</strong> to continue.</p>
                            </div>
                            <div className="card-body pt-4 text-center">
                                <button className="btn btn-primary" onClick={verify_account} type="button" >Verify</button>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>

        
    )
}

export default connect(null, { verify })(Activate);